html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
 margin: 0;
 padding: 0;
 border: 0;
 font-size: 100%;
 font: inherit;
 vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
 display: block;
}

body {
 line-height: 1;
}

ol,
ul {
 list-style: none;
}

blockquote,
q {
 quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
 content: '';
 content: none;
}

table {
 border-collapse: collapse;
 border-spacing: 0;
}

a {
 text-decoration: none;
 color: #fff;
}

* {
 margin: 0;
 padding: 0;
 box-sizing: border-box;
}

body {
 font-family: Quicksand;
 color: #fff;
 overflow: auto;
}

body::-webkit-scrollbar {
 width: 5px;
 height: 5px;
}

body::-webkit-scrollbar-thumb {
 background-color: #0e2941;
 border-radius: 50px;

}

header {
 width: 100%;
 height: 129px;
 background-color: #0e2941;
 
}

.head-title {
 display: flex;
 height: 75px;
}

.head {

 margin: 0 50px;
 height: 130px;
 padding: 0 15px;
}

.head img {
 width: auto;
 height: 75px;
 margin: 10px 55px 8px 0;
}

input[type=text] {
 margin-top: 30px;
 height: 40px;
 width: 681px;
 border: none;
 outline: none;
 border-radius: 5px;
 padding: 4px 14px;
 font-size: 18px;
}

input::placeholder {
 font-size: 18px;
}

.btn-search {
 position: relative;
 height: 40px;
 width: 50px;
}

.btn-search {
 position: absolute;
 background-color: #fff;
 border: none;
 border-radius: 5px;
 top: 30px;
 right: 365px;

}

.btn-search i {
 padding: 4px 11px 4px 12px;
 font-size: 26px;
 cursor: pointer;
}

.head-right {
 margin: 21px 0 10px 50px;
 width: 270px;
 height: 36px;
 color: #fff;

}
.head-right span a {
 color: #fff;
}
.head-right span:nth-child(1) {
 display: block;
 text-align: center;
 width: 270px;
 height: 18px;
 font-size: 16px;
 font-weight: 800;
 margin-bottom: 10 px;

}


.head-right>span:last-child {
 display: block;
 text-align: center;
 width: 270px;
 height: 18px;
 font-size: 16px;
 line-height: 1.2em;
 letter-spacing: 1.5px;
}

span>a:first-child {
 cursor: text;
}

.head-menu-nav {
 display: block;
}

.head-menu-nav {
 position: absolute;
 top: 21.5%;
 left: 50%;
 transform: translate(-50%, -50%);
 width: 350px;
 height: 36px;
}

.nav-content {
 display: flex;
 justify-content: space-between;
 align-items: center;
}

.nav-content li:first-child a {
 font-weight: bolder;
}

.nav-content a {
 font-size: 20px;
 padding: 8px 9px 10px 8px;
 color: #fff;
 background-color: none;
 margin: 0;
 line-height: 18px;
 letter-spacing: -0.5px;
}

.nav-content a:hover {
 background-color: #fff;
 color: black;
 font-weight: 800;
 border-radius: 5px 5px 0 0;
 transition: ease-in 0.2s;
}


.profile_account-cart {
 float: right;
 display: flex;
 justify-content: center;
 align-items: center;
 width: 250px;
 height: 40px;

}

.profile_account {
 display: grid;

}

.profile_account a {
 margin: 5px;
 font-size: 16px;
 color: #fff;
}
.profile_account a:hover{
 text-decoration: none;
 color: #bbb9b9;
}
.profile_account>a:last-child {
 font-weight: bolder;
}

.cart {
 position: relative;
 margin-left: 50px;

}

.cart i {
 font-size: 35px;
 color: #fff;

}

.cart::before {
 content: "";
 width: 31px;
 height: 31px;
 background-color: #FFCD0D;
 z-index: 0;
 border-radius: 50%;
 position: absolute;
 top: -10px;
 right: -10px;
}

.cart p {
 position: absolute;
 color: black;
 font-weight: bolder;
 z-index: 5;
 top: -7px;
 right: 0;
}